export default {
	data() {
		return {
			numberIndex: 0,
			numbers: [0, 1, 2, 3, 4, 5],
			partIndex: 0,
			partNumbers: [0, 1, 2, 3, 4, 5],
			info: {},
			para: {
				order_code: "",
				items: [],
				parts: [],
				imgs: []
			}
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.info = info;
		this.para.order_code = info.order_code;
		//获取维修单信息
		if (this.info.status == 3) {
			global.$http.post('/car/repair/repairInfo', {
				params: {
					order_code: this.info.order_code
				},
			}).then(res => {
				if (res.status === "0") {
					this.para.items.length = 0;
					res.data.items.forEach(c => {
						this.para.items.push({
							item_name: c.item_name,
							item_fee: c.item_fee,
							remark: c.remark
						});
					});
					this.numberIndex = res.data.items.length;

					this.para.parts.length = 0;
					res.data.parts.forEach(c => {
						this.para.parts.push({
							parts_name: c.parts_name,
							num: c.num,
							price: c.price
						});
					});
					this.partIndex = res.data.parts.length;
					
					this.para.imgs.length = 0;
					res.data.imgs.forEach(c => {
						this.para.imgs.push(c);
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	},
	methods: {
		ChangeNumber: function(e) {
			this.numberIndex = Number(e.detail.value);
			var orderCount = this.para.items.length;
			if (orderCount > this.numberIndex) {
				this.para.items.length = this.numberIndex;
			} else if (orderCount < this.numberIndex) {
				for (var i = 0; i < this.numberIndex - orderCount; i++) {
					this.para.items.push({
						item_name: "",
						item_fee: "",
						remark: ""
					});
				}
			}
		},
		ChangePartNumber: function(e) {
			this.partIndex = Number(e.detail.value);
			var orderCount = this.para.parts.length;
			if (orderCount > this.partIndex) {
				this.para.parts.length = this.partIndex;
			} else if (orderCount < this.partIndex) {
				for (var i = 0; i < this.partIndex - orderCount; i++) {
					this.para.parts.push({
						parts_name: "",
						num: "",
						price: ""
					});
				}
			}
		},
		ChooseImage: function() {
			uni.chooseImage({
				count: 5, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: (res) => {
					//上传图片
					uni.showLoading({
						title: '上传图片中',
						mask: false
					});
					global.$http.upload('/oos/upload', {
						name: 'file',
						filePath: res.tempFilePaths[0]
					}).then(res => {
						uni.hideLoading();
						if (res.status === "0") {
							this.para.imgs.push(res.data);
							uni.showToast({
								icon: 'none',
								title: '上传成功'
							});
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.hideLoading();
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				}
			});
		},
		DelImg: function(e) {
			this.para.imgs.splice(e.currentTarget.dataset.index, 1);
			this.para.img = "";
		},
		Submit: function(e) {
			//验证数据
			for (var i = 0; i < this.para.items.length; i++) {
				var item = this.para.items[i];
				if (item.item_name.length <= 0) {
					uni.showToast({
						icon: 'none',
						title: '维修项目' + (i + 1) + '：不能为空'
					});
					return;
				}
				if (item.item_fee.length <= 0) {
					uni.showToast({
						icon: 'none',
						title: '维修项目' + (i + 1) + '：工时费不能为空'
					});
					return;
				}
			}
			for (var i = 0; i < this.para.parts.length; i++) {
				var part = this.para.parts[i];
				if (part.parts_name.length <= 0) {
					uni.showToast({
						icon: 'none',
						title: '配件' + (i + 1) + '：不能为空'
					});
					return;
				}
				if (part.price.length <= 0) {
					uni.showToast({
						icon: 'none',
						title: '配件' + (i + 1) + '：单价不能为空'
					});
					return;
				}
				if (part.num.length <= 0) {
					uni.showToast({
						icon: 'none',
						title: '配件' + (i + 1) + '：数量不能为空'
					});
					return;
				}
			}
			if(this.para.items.length == 0 && this.para.parts.length == 0){
				uni.showToast({
					icon: 'none',
					title: '维修项目和配件不能都为空'
				});
				return;
			}
			//提交数据
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/car/repair/createRepairOrder', {
				header: {
					ContentType: 'text/plain'
				},
				data: JSON.stringify(this.para)
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						icon: 'none',
						title: '提交成功'
					});
					uni.navigateBack();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	}
}
